@media (max-width:1360px){
  .header-content h1{
    font-size:6rem;
  }

  .offer-info h2{
    font-size:6rem;
  }
}

@media (max-width:1190px){
  header{
    background-size:1150px!important;
    min-height:100vh;
  }
  .header-content h1 > span{
    display:inline-block;
    width:100%;
  }

  #features{
    height:100%;
    padding:100px 0;
  }

  #about{
    min-height:100vh;
    flex-wrap:wrap;
    height:auto;
  }

  .about-image{
    width:90%;
    text-align:center;
  }

  .about-text{
    margin-top:0;
    text-align:center;
  }

  #contact{
    margin-top:136px;
  }
}

@media (max-width:970px){
  .menu-icon{
    display:block;
  }

  nav{
    justify-content:space-between;
    height:70px;
  }

  nav .logo{
    margin-left:30px;
  }

  nav .menu-icon{
    margin-right:30px;
    cursor:pointer;
    position:relative;
    z-index:111;
  }

  nav .menu-icon{
    display:block;
  }

  nav .nav-icon{
    font-size:24px;
  }

  .menu{
    display:none!important;
    position:absolute;
    top:70px;
    left:0;
    width:100%;
    padding:0;
    margin:0;
    background:#000;
  }

  .menu li{
    width:100%;
    text-align:center;
  }

  .menu li a{
    width:100%;
    height:40px;
  }

  /* Menu block */
  .menu-btn:checked ~ .menu{
    display:block!important;
  }

  header{
    background-size:950px!important;
  }

  #offer{
    background-size:950px!important;
  }

  .about-text{
    width:60%;
  }
}